const canvas = document.getElementById('bg')
const ctx = canvas.getContext('2d')

// Ëé∑ÂèñÂΩìÂâçËßÜÂõæÁöÑÂÆΩÂ∫¶ÂíåÈ´òÂ∫¶
let aw = document.documentElement.clientWidth || document.body.clientWidth;
let ah = document.documentElement.clientHeight || document.body.clientHeight;
canvas.width = aw;
canvas.height = ah;

// ÂõæÁâáÁõ∏ÂÖ≥ÂèÇÊï∞
var count = 0,images = {},imgNum = 0;
// ÊñáÂ≠óÂÆΩÈ´ò
var textW = 210, textH = 70;
// È°∂ÈÉ®ÊòüÊòü„ÄÅËÉåÊôØÊòüÊòü
var topStars = [], stars = [];

init();
loadImgs();
requestAnimationFrame(animate);

function init() {
    for (let i = 0; i < 5; i++) {
        topStars.push(new TopStar((aw / 5) * i + 25));
    }
    for (let i = 0; i < 60; i++) {
        stars.push(new Star());
    }
}

function loadImgs() {
    var img = new Image();
    img.src = './crstal/starshaow.png';
    var sources = {
        starshaow: "./crstal/starshaow.png",
        text: "./crstal/text.png",
        leaf: "./crstal/leaf.png",
    };
    //Ë∞ÉÁî®ÂõæÁâáÈ¢ÑÂä†ËΩΩ
    loadImages(sources, function (images) { });
}


function animate() {
    ctx.clearRect(0, 0, aw, ah);

    // ÁîªÊñáÂ≠ó
    ctx.drawImage(images.text, aw / 2 - textW / 2, 150, textW, textH);
    // Âè∂Â≠ê
    ctx.save();//‰øùÂ≠òÁä∂ÊÄÅ
    ctx.drawImage(images.leaf, 40, 490, 150, 120);
    
    ctx.scale(-1, 1);
    ctx.translate(-aw, 0);
    ctx.drawImage(images.leaf, 30, 490, 150, 120);
    ctx.restore(); //ÊÅ¢Â§çÁä∂ÊÄÅ
   
    for (let i = 0; i < topStars.length; i++) {
        topStars[i].draw();
        topStars[i].update();
    }

    // Â∞èÊòüÊòü
    for (let i = 0; i < stars.length; i++) {
        stars[i].draw();
        stars[i].update();
    }

    requestAnimationFrame(animate);
}

/** 
 * ÂàõÂª∫È°∂ÈÉ®ÊòüÊòü
 * */ 
function TopStar(x) {
    this.w = 30;
    this.x = x;
    this.y = 30;
    this.angle = getRandom(0, 45);
    this.rotateDir = 'L';
}

TopStar.prototype.draw = function () {
    ctx.save();//‰øùÂ≠òÁä∂ÊÄÅ
    let centerX = this.x + this.w / 2; // ‰∏≠ÂøÉÁÇπX
    let centerY = 0;  // ‰∏≠ÂøÉÁÇπY
    ctx.translate(centerX, centerY);//ËÆæÁΩÆÁîªÂ∏É‰∏äÁöÑ(0,0)‰ΩçÁΩÆÔºå‰πüÂ∞±ÊòØÊóãËΩ¨ÁöÑ‰∏≠ÂøÉÁÇπ
    ctx.rotate((this.angle * Math.PI) / 180);
    ctx.translate(-centerX, centerY);//ËÆæÁΩÆÁîªÂ∏É‰∏äÁöÑ(0,0)‰ΩçÁΩÆÔºå‰πüÂ∞±ÊòØÊóãËΩ¨ÁöÑ‰∏≠ÂøÉÁÇπ
    ctx.beginPath();
    ctx.setLineDash([2, 3]);
    ctx.moveTo(centerX, 0);
    ctx.lineTo(centerX, this.y + this.w / 2);
    ctx.lineWidth = 0.5;
    ctx.strokeStyle = '#dfa814';
    ctx.stroke();
    // dots
    ctx.drawImage(images.starshaow, this.x, this.y, this.w, this.w);
    ctx.restore(); //ÊÅ¢Â§çÁä∂ÊÄÅ
}

TopStar.prototype.update = function () {
    if (this.rotateDir === 'L') {
        this.angle += 0.5;
        if (this.angle > 45) {
            this.rotateDir = 'R'
        }
    }

    if (this.rotateDir === 'R') {
        this.angle -= 0.5;
        if (this.angle < -45) {
            this.rotateDir = 'L'
        }
    }

}

/**
 * ÂàõÂª∫ËÉåÊôØÊòüÊòü
 *  */ 

function Star() {
    this.x = getRandom(0, aw);
    this.y = getRandom(0, ah / 3)
    this.starSize = 1;
    this.maxSize = getRandom(1, 4); // Á∫µËΩ¥ÈïøÂ∫¶ÔºåÊ®™ËΩ¥ÈïøÂ∫¶ÊòØ0.25ÂÄç
    this.dir = 'up';
    this.starSpeed = 0.06;
}

Star.prototype.draw = function () {
    var color = '#FFCCBC'
    // Âõ¥Áªï‰∏≠ÂøÉÁîüÊàêÂõõ‰∏™Â∞è‰∏âËßíÂΩ¢
    ctx.beginPath();
    ctx.moveTo(this.x, this.y - this.starSize);
    ctx.lineTo(this.x - this.starSize * 0.25, this.y);
    ctx.lineTo(this.x + this.starSize * 0.25, this.y);
    ctx.fillStyle = color;
    ctx.fill();

    ctx.beginPath();
    ctx.moveTo(this.x + this.starSize, this.y);
    ctx.lineTo(this.x, this.y - this.starSize * 0.25);
    ctx.lineTo(this.x, this.y + this.starSize * 0.25);
    ctx.fillStyle = color;
    ctx.fill();

    ctx.beginPath();
    ctx.moveTo(this.x, this.y + this.starSize);
    ctx.lineTo(this.x - this.starSize * 0.25, this.y);
    ctx.lineTo(this.x + this.starSize * 0.25, this.y);
    ctx.fillStyle = color;
    ctx.fill();

    ctx.beginPath();
    ctx.moveTo(this.x - this.starSize, this.y);
    ctx.lineTo(this.x, this.y - this.starSize * 0.25);
    ctx.lineTo(this.x, this.y + this.starSize * 0.25);
    ctx.fillStyle = color;
    ctx.fill();
}

Star.prototype.update = function () {
    if (this.dir === 'up') {
        this.starSize = this.starSize + this.starSpeed;
        if (this.starSize >= this.maxSize) {
            this.dir = 'down';
        }
    } else {
        this.starSize = this.starSize - this.starSpeed;
        if (this.starSize < 2) {
            this.dir = 'up';
        }
    }
}

function loadImages(sources, callback) {
    for (src in sources) {
        imgNum++;
    }
    for (src in sources) {
        images[src] = new Image();
        images[src].onload = function () {
            if (++count >= imgNum) {
                callback(images);
            }
        }
        images[src].src = sources[src];
    }
}

// Ëé∑ÂèñÈöèÊú∫Êï∞
function getRandom(n, m) {
    return Math.floor(Math.random() * (m - n + 1)) + n;
}
